import React from "react";
import { injectIntl, IntlShape } from "react-intl";
import useStyles from "./styles";
import { useRenderSteps } from "../../contexts/renderSteps";
import { useUserData } from "../../shared/hooks/UserContext";
import translate from "../../shared/i18n/translate";
import Title from "../../shared/components/Title";
import SubTitle from "../../shared/components/SubTitle";
import BtnsSocialLogin from "../../shared/components/BtnsSocialLogin";
import { Link } from "@material-ui/core";
import Terms from "../../shared/components/Terms";
import HeaderLogin from "../../shared/components/HeaderLogin";
import useGa from '../../../utils/useGa'

interface SocialLoginAccountProps {
    onCloseClick?(): void;
    isModal?: boolean;
    intl: IntlShape;
}


const SocialLoginAccount: React.FC<SocialLoginAccountProps> = ({ onCloseClick, isModal }) => {
    const classes = useStyles({});
    const { renderNewStep } = useRenderSteps();
    const { user } = useUserData();
    const { saveOnGa } = useGa();

    const handleClick = () => {
        saveOnGa({
            event: "login_event",
            category: "Account",
            action: "Registration",
            label: "alert :: confirm re-registration"
        })
        renderNewStep("NewRegister")
    }

    return (
        <>
            <HeaderLogin isModal={isModal} previousStep="MainScreen" onCloseClick={onCloseClick} />
            <div className={classes.root}>
                <Title className={classes.title}>
                    {translate({ id: "SocialLoginAccount.title" })}
                </Title>
                <SubTitle className={classes.subTitle}>
                    {translate({ id: "SocialLoginAccount.subTitle" })}
                    <b>{` ${user.email}`}</b>
                </SubTitle>
                <div className={classes.buttonContainer}>
                    <BtnsSocialLogin />
                    <Link className={classes.link} onClick={handleClick}>
                        <SubTitle variant="body2">
                            <b>{translate({ id: "SocialLoginAccount.btn" })}</b>
                        </SubTitle>
                    </Link>
                </div>
            </div>
            <Terms />
        </>
    );
};

export default injectIntl(SocialLoginAccount);
